<template>
	<div class="login-page">
		<van-cell-group>
			<van-field v-model="phone" type="tel" maxlength="11" center clearable name="phone" placeholder="请输入手机号码" />
			<van-field v-model="verifycode" type="number" maxlength="4" center clearable placeholder="验证码">
				<van-button slot="button" size="small" type="primary" @click="sendVerifycode" :disabled="!disabledCodeBtn">{{codeText}}</van-button>
			</van-field>
		</van-cell-group>
		<div class="page-footer">
			<van-button round block type="primary" @click="login">登录</van-button>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'login',
		data() {
			return {
				phone: '',
				verifycode: '',
				codeText: '获取验证码',
				disabledCodeBtn: true
			}
		},
		methods: {
			// 向后台要验证码方法
			async sendVerifycode() {
				if(this.verifyPhone()) {
					this.$toast(this.verifyPhone())
				} else {
					this.countDown(60)
					let params = {
						phone: this.phone
					}
					let res = await this.$Request.get('/student/getVerificationCode.do', params)
					if(res && res.code === 0) {
						this.$toast.success('发送成功')
					}
				}
			},
			// 手机号码校验方法
			verifyPhone() {
				if(!this.phone) {
					return '请输入手机号'
				} else if(this.phone.length !== 11) {
					return '请输入11位手机号'
				} else {
					return false
				}
			},
			// 验证码校验方法
			verifyCode() {
				if(!this.verifycode) {
					return '请输入验证码'
				} else {
					return false
				}
			},
			// 倒计时方法
			countDown(time) {
				if(time === 0) {
					this.disabledCodeBtn = true
					this.codeText = "获取验证码"
					return
				} else {
					this.disabledCodeBtn = false
					this.codeText = '重新发送(' + time + ')'
					time--
				}
				setTimeout(() => {
					this.countDown(time)
				}, 1000)
			},
			// 登录
			async login() {
				console.log(this.verifyPhone(), this.verifyCode())
				if(this.verifyPhone() || this.verifyCode()) {
					this.$toast(this.verifyPhone() || this.verifyCode())
				} else {
					console.log(6666)
					let params = {
						phone: this.phone,
						verificationCode: this.verifycode
					}
					let res = await this.$Request.get('/student/queryStudentsByWeChat.do', params)
					if(res && res.code === 0 && res.list) {
						let info = res.list
						 // 设置登录状态有效期一天
						this.$Utils.setItem("flag", 'islogin', 1)
						this.$Utils.setItem("info", info, 1)
						var redirectUrl = this.$route.query.returnUrl
						// 重定向地址存在是跳转重定向地址，不存在时跳转首页
						if(redirectUrl) {
							this.$router.replace({
								path: redirectUrl
							})
						} else {
							this.$router.replace({
								path: '/index'
							})
						}
					}
				}
			}
		}
	}
</script>

<style>

</style>


